import React, { useState, useEffect } from 'react';
import { Group, Header, InfoRow, ModalCard, ModalPage, ModalPageHeader, ModalRoot, SelectMimicry, Textarea, Button, FormLayout, Select, CustomSelectOption, FormItem, FormLayoutGroup, Input } from '@vkontakte/vkui'



let ModalCommon = (props) => {
    //     
    let [selectedYear, setSelectedYear] = useState(" ")
    let [selectedCity, setSelectedCity] = useState(" ")
    let [bidCity, setBidCity] = useState("")

	useEffect(() => {
		//   
		
    }, [])
    

    return (
        <ModalRoot activeModal={props.modalName}>
            <ModalPage id="CitySelect"
                header={
                <ModalPageHeader>
                
              </ModalPageHeader>
            }
          >
                <Group>
                    <InfoRow>
                           : {props.data.title}
                    </InfoRow>
                    <Header mode="secondary">  ?</Header>
                    <InfoRow top="">            
                        <SelectMimicry placeholder=" " />
                    </InfoRow>
                </Group>    
            </ModalPage>

            <ModalCard
                id="Error"
                onClose={props.Close}
                header={props.data.message ? props.data.message : "  "}
                actions={<Button size="l" mode="primary" onClick={props.Close}></Button>}>
                    {/* <Textarea defaultValue="" /> */}
            </ModalCard>

            <ModalCard
                id="MyProfile"
                onClose={props.Close}
                header={props.data.Name ? props.data.Name : "  "}
                actions={<Button size="l" mode="primary" onClick={props.Close}></Button>}>
                    {/* <Textarea defaultValue="" /> */}
                    {
                        (props.data.Name) ?
                        `: ${props.data.Name} \r\n
                        : ${props.data.Surname} \r\n
                         : ${props.data.Birth} \r\n
                        : ${props.data.Register} \r\n
                        : ${props.data.City.Name}
                        ` : ""
                    }
            </ModalCard>
            <ModalCard
                id="SelectBirth"
                // onClose={props.Close}
                header="   "
                actions={<Button size="l" mode="primary" onClick={() => props.action({...props.data, bdate: props.data.bdate + ((!isNaN(selectedYear)) && (selectedYear > 0) && "." + selectedYear)})}></Button>}>
                    <FormLayout>
                        <FormLayoutGroup>
                        <InfoRow header="   ">
                                    .     .
                        </InfoRow>
                            <FormItem top=" ">            
                            <Select
                                placeholder=" " 
                                onChange={(e) => {
                                    setSelectedYear(+e.currentTarget.value)
                                    }}
                                options={[...((start, count) => {
                                    
                                        let current = start;
                                        let result = [];
                                        while (current < start + count){
                                            result.push(current);
                                            current += 1;
                                        }
                                        
                                        return result
                                    })(new Date().getFullYear() - 100, 90)
                                ].map(year => ({ label: year, value: year }))}
                                renderOption={({ option, ...restProps }) => <CustomSelectOption {...restProps} />}
                                />
                            </FormItem>
                        </FormLayoutGroup>
                    </FormLayout>
            </ModalCard>
            <ModalCard
                id="SelectCity"
                // onClose={props.Close}
                header=" "
                actions={<Button size="l" mode="primary" onClick={() => {
                        debugger
                        props.action({...props.data.profile, CityUmbracoId: selectedCity})
                        }
                    }></Button>}>
                    <FormLayout>
                        <FormLayoutGroup>
                            <InfoRow header="  ">
                                ,      <b style={{color: 'red'}}>{props.data.profile ? props.data.profile.CityName : ""}</b>        . 
                                      ,       .
                            </InfoRow>
                            <FormItem top="">            
                            <Select
                                placeholder=" " 
                                onChange={(e) => {
                                    debugger
                                    setSelectedCity(+e.currentTarget.value)
                                    }}
                                options={props.data.cities && props.data.cities.map(city => {
                                    return { label: city.CityUmbracoName ?? city.CityUmbracoId, value: city.CityUmbracoId }
                                }
                                )}
                                renderOption={({ option, ...restProps }) => <CustomSelectOption {...restProps} />}
                                />
                            </FormItem>
                        </FormLayoutGroup>
                        <InfoRow></InfoRow>
                        <FormLayoutGroup>
                            <InfoRow header="   ">
                                        
                                (         ...     , )
                            </InfoRow>
                            <Input value={bidCity} onChange={(e) => setBidCity(e.currentTarget.value)} type="text"></Input>
                        </FormLayoutGroup>
                    </FormLayout>
            </ModalCard>
      </ModalRoot>
    )
}


export default ModalCommon