const initState = {

}


let ampluaReducer = (state = initState, action) => 
{
    switch (action.type){
        case ANY_ACTION_TYPE: {
            return state;
        }
        default: {
            return state;
        }
    }
}


export const anyActionCreator = (val) => {
    return {
        type: ANY_ACTION_TYPE,
        anyVal: val
    }
}


export default ampluaReducer;