import React, {useState} from 'react'
import { Button, CellButton, Input, RichCell } from '@vkontakte/vkui'




const BidListItem = (props) => {

    let [adminText, setAdminText] = useState("");


    return (
            <RichCell key={props.KeyId != -1 ? props.KeyId : null} 
            actions={
                <>
                    <Button mode="primary" onClick={() => props.Accept(props.Item)}></Button>
                    <Input value={adminText} onChange={(e) => setAdminText(e.currentTarget.value)} placeholder="  "></Input>
                    <Button mode="destructive" onClick={() => props.Decline(props.Item, adminText)}></Button>
                </>
            }
            text={`    ${props.Item.TeamName}  ${props.Item.TournamentGroup.Name} /`}
            caption={`  ${props.Item.UserProfile.Surname} ${props.Item.UserProfile.Name}`}
            >
                 {props.Item.Team.Name}
            </RichCell>
    )

}


export default BidListItem