import React, { useEffect, useState } from 'react'
import { RichCell, Avatar, FormLayout, FormItem, Input, InfoRow, Group, DatePicker, Textarea, File, CellButton, Button, Header, List, Cell, TabsItem, Tabs, View, Panel } from '@vkontakte/vkui'
import { defaultPhotoPath } from '../../../../store/dataTypes/common'
import {
    setTournamentWhenBegin, setTournamentWhenEnd, setTournamentName, setTournamentReglament, setTournamentDetails, delGroupFromTournamentByKeyId, deleteTournamentGroup,
    editGroupInTournament, addTournamentGroup, resetTournament, saveSelectedTournament, getTournamentNewBids, acceptTeamToTournamentBid, declineTeamToTournamentBid,
} from '../../../../store/tournamentsReducer'
import { Icon24Camera, Icon28AddOutline } from '@vkontakte/icons';
import { connect } from 'react-redux';
import ListItem from '../ListItem/ListItem';
import BidListItem from '../ListItem/BidListItem';




const TournamentItem = (props) => {
    let currentDate = new Date();
    let [tempGroupName, setTempGroupName] = useState("");
    let [currentTab, setCurrentTab] = useState("info");

    useEffect(() => {
        props.getTournamentNewBids(props.tournaments.selected, props.myProfile)
    }, props.tournaments.selected)

    const addToTournament = () => {
        debugger
        if (tempGroupName.trim() != "") {
            //addGroupToTournament
            props.addTournamentGroup(props.tournaments.selected, props.myProfile, { Id: -1, Name: tempGroupName });
            setTempGroupName("");
        }
    }

    const DelGroupFromTournament = (keyId, groupId) => {
        //props.tournaments.selected.Id, item.KeyId, item.Id
        debugger
        if (groupId < 0)
            props.delGroupFromTournamentByKeyId(props.tournaments.selected.Id, keyId); // (      )
        else
            props.deleteTournamentGroup(props.tournaments.selected, props.myProfile, groupId); //    , ..      ( )
    }
    
    const AcceptBid = (item) => {
        props.acceptTeamToTournamentBid(item, props.tournaments.selected, props.myProfile)
        
    }

    const DeclineBid = (item, text) => {
        props.declineTeamToTournamentBid(item, props.tournaments.selected, props.myProfile, text);
        
    }

    switch (props.mode) {
        case "view": {
            return (
                <>
                    <FormItem top=" ">
                        <InfoRow>{props.myProfile.CityUmbracoName}</InfoRow>
                    </FormItem>
                    <FormItem top=" " bottom="    ">
                        <InfoRow>{props.tournaments.selected.Name}</InfoRow>
                    </FormItem>
                    <FormItem top=" ">
                        <InfoRow>{props.tournaments.selected.WhenBegin}</InfoRow>
                    </FormItem>
                    <FormItem top=" ">
                        <InfoRow>{props.tournaments.selected.WhenEnd}</InfoRow>
                    </FormItem>
                    <FormItem top=" ">
                        <InfoRow>{props.tournaments.selected.Details}</InfoRow>
                    </FormItem>
                    <FormItem top=" ">
                        <InfoRow>{props.tournaments.selected.Reglament}</InfoRow>
                    </FormItem>
                    <Group header={<Header mode="secondary"></Header>}>
                        {(props.tournaments.selected.TournamentGroups && props.tournaments.selected.TournamentGroups.length > 0) ?
                            <List>
                                {props.tournaments.selected.TournamentGroups.map((item) => <InfoRow>{item.Name}</InfoRow>)}
                            </List>
                            :
                            <FormItem>
                                <InfoRow> </InfoRow>
                            </FormItem>
                        }
                    </Group>
                </>
            )
        }; break;
        case "add": {
            return (
                <Group>
                    <Header> </Header>
                    <FormLayout>
                        <FormItem top=" ">
                            <InfoRow>{props.myProfile.CityUmbracoName}</InfoRow>
                        </FormItem>
                        <FormItem top=" ">
                            <Input type="text" defaultValue={props.tournaments.selected.Name} value={props.tournaments.selected.Name} onChange={e => props.setTournamentName(e.currentTarget.value)} placeholder=", II    2023   ..." />
                        </FormItem>
                        <FormItem top=" ">
                            <DatePicker
                                min={{ day: 1, month: 1, year: currentDate.getFullYear() - 1 }}
                                max={{ day: 1, month: 1, year: currentDate.getFullYear() + 1 }}
                                defaultValue={props.tournaments.selected.WhenBegin}
                                value={props.tournaments.selected.WhenBegin}
                                onDateChange={value => props.setTournamentWhenBegin(value)}
                            />
                        </FormItem>
                        <FormItem top=" ">
                            <DatePicker
                                min={{ day: 1, month: 1, year: currentDate.getFullYear() - 1 }}
                                max={{ day: 1, month: 1, year: currentDate.getFullYear() + 1 }}
                                defaultValue={props.tournaments.selected.WhenEnd}
                                value={props.tournaments.selected.WhenEnd}
                                onDateChange={value => props.setTournamentWhenEnd(value)}
                            />
                        </FormItem>
                        <FormItem top=" ">
                            <Textarea defaultValue={props.tournaments.selected.Details} value={props.tournaments.selected.Details} onChange={e => props.setTournamentDetails(e.currentTarget.value)} placeholder=" " />
                        </FormItem>
                        <FormItem top=" ">
                            <Textarea defaultValue={props.tournaments.selected.Reglament} value={props.tournaments.selected.Reglament} placeholder=" " onChange={e => props.setTournamentReglament(e.currentTarget.value)} />
                        </FormItem>
                        {/* <FormItem top="  ">
                            <File before={<Icon24Camera />} controlSize="m">
                                 
                            </File>
                        </FormItem> */}
                        <Group header={<Header mode="secondary"></Header>}>
                            {(props.tournaments.selected.TournamentGroups && props.tournaments.selected.TournamentGroups.length > 0) ?
                                <List>
                                    {props.tournaments.selected.TournamentGroups.map((item) => <ListItem KeyId={-1} Delete={() => DelGroupFromTournament(item.KeyId, item.Id)} Name={item.Name}></ListItem>)}
                                </List>
                                :
                                <FormItem>
                                    <InfoRow> </InfoRow>
                                </FormItem>
                            }
                        </Group>
                        <FormItem top=" /">
                            <Input type="text" defaultValue={tempGroupName} value={tempGroupName} onChange={e => setTempGroupName(e.currentTarget.value)} placeholder=", ,  1" />
                            <CellButton onClick={addToTournament} before={<Icon28AddOutline />}> /</CellButton>
                        </FormItem>
                        <FormItem top="">
                            <Button onClick={() => props.saveSelectedTournament(props.tournaments.selected, props.myProfile)}></Button>
                            <Button onClick={props.resetTournament} mode="secondary"></Button>
                        </FormItem>
                    </FormLayout>
                </Group>
            )
        }; break;
        case "edit": {
            return (
                <View activePanel={currentTab}>
                    <Panel id="info">
                        <Group>
                            <Header> </Header>
                            <Tabs mode="buttons">
                                <TabsItem onClick={() => setCurrentTab("info")}></TabsItem>
                                <TabsItem onClick={() => setCurrentTab("bids")}></TabsItem>
                            </Tabs>
                            <FormLayout>
                                <FormItem top=" ">
                                    <InfoRow>{props.myProfile.CityUmbracoName}</InfoRow>
                                </FormItem>
                                <FormItem top=" ">
                                    <Input type="text" defaultValue={props.tournaments.selected.Name} onChange={e => props.setTournamentName(e.currentTarget.value)} placeholder=", II    2023   ..." />
                                </FormItem>
                                <FormItem top=" ">
                                    <DatePicker
                                        min={{ day: 1, month: 1, year: currentDate.getFullYear() - 1 }}
                                        max={{ day: 1, month: 1, year: currentDate.getFullYear() + 1 }}
                                        defaultValue={props.tournaments.selected.WhenBegin}
                                        value={props.tournaments.selected.WhenBegin}
                                        onDateChange={value => props.setTournamentWhenBegin(value)}
                                    />
                                </FormItem>
                                <FormItem top=" ">
                                    <DatePicker
                                        min={{ day: 1, month: 1, year: currentDate.getFullYear() - 1 }}
                                        max={{ day: 1, month: 1, year: currentDate.getFullYear() + 1 }}
                                        defaultValue={props.tournaments.selected.WhenEnd}
                                        value={props.tournaments.selected.WhenBegin}
                                        onDateChange={value => props.setTournamentWhenEnd(value)}
                                    />
                                </FormItem>
                                <FormItem top=" ">
                                    <Textarea defaultValue={props.tournaments.selected.Details} onChange={e => props.setTournamentDetails(e.currentTarget.value)} placeholder=" " />
                                </FormItem>
                                <FormItem top=" ">
                                    <Textarea defaultValue={props.tournaments.selected.Reglament} placeholder=" " onChange={e => props.setTournamentReglament(e.currentTarget.value)} />
                                </FormItem>
                                {/* <FormItem top="  ">
                            <File before={<Icon24Camera />} controlSize="m">
                                 
                            </File>
                        </FormItem> */}
                                <Group header={<Header mode="secondary"></Header>}>
                                    {(props.tournaments.selected.TournamentGroups && props.tournaments.selected.TournamentGroups.length > 0) ?
                                        <List>
                                            {props.tournaments.selected.TournamentGroups.map((item) => <ListItem KeyId={item.KeyId} Delete={() => DelGroupFromTournament(item.KeyId, item.Id)} Name={item.Name}></ListItem>)}
                                        </List>
                                        :
                                        <FormItem>
                                            <InfoRow> </InfoRow>
                                        </FormItem>
                                    }
                                </Group>
                                <FormItem top=" /">
                                    <Input type="text" defaultValue={tempGroupName} value={tempGroupName} onChange={e => setTempGroupName(e.currentTarget.value)} placeholder=",  1" />
                                    <CellButton onClick={addToTournament} before={<Icon28AddOutline />}> </CellButton>
                                </FormItem>
                                <FormItem top="">
                                    <Button onClick={() => props.saveSelectedTournament(props.tournaments.selected, props.myProfile)}> </Button>
                                    {/* <Button onClick={props.resetTournament} mode="secondary"></Button> */}
                                </FormItem>
                            </FormLayout>
                        </Group>
                    </Panel>
                    <Panel id="bids">
                        <Header>  </Header>
                        <Tabs mode="buttons">
                            <TabsItem onClick={() => setCurrentTab("info")}></TabsItem>
                            <TabsItem onClick={() => setCurrentTab("bids")}></TabsItem>
                        </Tabs>
                        <FormLayout>
                            <FormItem top=" ">
                                <InfoRow>{props.myProfile.CityUmbracoName}</InfoRow>
                            </FormItem>
                            <Group header={<Header mode="secondary"></Header>}>
                                {(props.tournaments.bidsNew && props.tournaments.bidsNew.length > 0) ?
                                    <List>
                                        {props.tournaments.bidsNew.map((item) => 
                                        <BidListItem
                                            KeyId={item.KeyId} 
                                            Accept={AcceptBid}
                                            Decline={DeclineBid}
                                            Item={item}
                                            >

                                        </BidListItem>)}
                                    </List>
                                    :
                                    <FormItem>
                                        <InfoRow>  </InfoRow>
                                    </FormItem>
                                }
                            </Group>
                        </FormLayout>
                    </Panel>
                </View>
                // <Tabs>
                //     <TabsItem>

                //     </TabsItem>
                // </Tabs>

            )
        }; break;
    }


}

const mapStateToProps = (state) => {
    return {
        tournaments: state.tournamentsEntity,
        SelectedName: state.tournamentsEntity.selected.Name,
        cities: state.cityEntity.cities,
        myProfile: state.profileEntity.myProfile,
    }
}

export default connect(mapStateToProps, {
    setTournamentWhenBegin, setTournamentWhenEnd, setTournamentName, setTournamentReglament, setTournamentDetails, acceptTeamToTournamentBid, declineTeamToTournamentBid, 
    delGroupFromTournamentByKeyId, deleteTournamentGroup, editGroupInTournament, addTournamentGroup, resetTournament, saveSelectedTournament, getTournamentNewBids, 
})(TournamentItem)